* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul,
ol {
    list-style: none;
}

body {
    background: $body;
    color: $color;
    font-family: $font-play;
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-thumb {
    background: $menu-link;
    border-radius: 4px;
    
}

.container {
    @include container;
}

::before,
::after {
    font-family: $font-aw;
    font-weight: 300;
}

.icon {
    &-phone::before {
        content: '\f879';
        line-height: 14px;
        font-size: 14px;
    }

    &-enter::before {
        content: '\f090';
        font-size: 14px;
    }

    &-shop::before {
        content: '\f07a';
        font-size: 16px;
    }

    &-search::before {
        content: '\f002';
        font-size: 17px;
    }

    &-burger::before {
        content: '\f0c9';
    }
}

.logo {
    font-weight: 700;
    font-family: $font-post;
    color: inherit;
    font-size: 36px;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.title {
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 70px;
    text-align: center;
    
    @include media($xs) {
        font-size: 24px;
    }
    
    &::after {
        content: '';
        display: block;
        height: 1px;
        width: 120px;
        position: absolute;
        bottom: -30px;
        background: #fff;
        right: 0;
        left: 0;
        margin: 0 auto;
    }
}

.card {
    
    &__box {
        background: $card-bg;
        min-height: 325px;
        padding: 22px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 25px;
    }
    
    &__img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    
    &__title {
        font-size: 18px;
        margin-bottom: 10px;
        font-weight: 400;
    }
    
    &__price {
        font-weight: 700;
        color: $card-price;
    }
}