.collection {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    text-align: center;

    @include media($lg) {
        grid-template-columns: repeat(1, 100%);
        gap: 30px;
        padding: 30px 30px;

        &>:last-of-type {
            grid-row: 3;
        }
    }
    

    &__left {
        @extend .flex;
        margin-left: 80px;
        min-height: 730px;
        position: relative;

        @include media($lg) {
            margin-left: 0;
            min-height: 600px;


        }

        @include media($xs) {
            min-height: 350px;
        }
    }

    &__right {
        @extend .flex;
        margin-right: 80px;
        min-height: 600px;
        position: relative;

        @include media($lg) {
            margin-right: 0;

            &:last-of-type {
                min-height: auto;
            }
        }

        @include media($xs) {
            min-height: 350px;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 50px;
        text-align: left;
        // place-content: center;

        @include media(1700px) {
            grid-template-columns: repeat(3, 260px);
            // overflow: hidden;
            padding-bottom: 20px;
            overflow-x: scroll;
            width: 100%;
            scroll-snap-type: x mandatory;

            .card {
                scroll-snap-align: center;
            }
        }

        @include media($lg) {
            grid-template-columns: repeat(3, 1fr);
        }

        @include media(800px) {
            grid-template-columns: repeat(3, 260px);
        }

        @include media($xs) {
            grid-template-columns: repeat(1, minmax(100px, 260px));
            justify-content: center;
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;

        &_left {
            object-position: left;
        }
    }

    &__gradient {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: $gradient;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-direction: column;
        padding-bottom: 100px;

        @include media($xs) {
            padding-bottom: 20px;
        }
    }

    &__link {
        font-size: 18px;
        text-transform: uppercase;
        font-family: $font-pt;
        padding: 12px 28px;
        border: 1px solid $color;
        color: $color;
        letter-spacing: 0.1em;
    }

    &__desc {
        max-width: 471px;
        width: 100%;
        font-size: 14px;
        line-height: 150%;
        font-family: $font-pt;
        margin-bottom: 66px;
    }
}