.about {
    background: url(../img/about-bg.jpg) center /cover;
    padding: 80px 0 101px;

    &__content {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;

        @include media($md) {
            grid-template-columns: repeat(2, 1fr);
        }
        @include media($sm) {
            grid-template-columns: repeat(1, 1fr);
        }
    }

    &__title {
        font-size: 24px;
        font-weight: 400;
        text-transform: uppercase;
        margin-bottom: 40px;
    }

    &__desc {
        font-size: 14px;
        font-family: $font-pt;
        line-height: 150%;
    }

    &__menu {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__link {
        margin-right: 20px;
        margin-bottom: 20px;
        padding: 10px 20px;
        color: $about-link-color;
        font-size: 14px;
        line-height: 150%;
        font-family: $font-pt;
        background: $about-link-bg;
        display: block;
        
        &_upp {
            border: none;
            margin: 0;
            cursor: pointer;
            outline: none;
        }
    }

    &__form {
        display: flex;
        align-items: center;
        margin-top: 37px;
    }

    &__input {
        padding: 10px 15px;
        color: $about-input-color;
        background: $about-input-bg;
        border: none;
        outline: none;
        font-size: 14px;
        line-height: 150%;
        font-family: $font-pt;

        &::placeholder {
            color: $about-input-color;
            font-family: $font-pt;
            font-size: 14px;
            line-height: 150%;
        }
    }
}