.brands {
    background: $brands-bg;
    padding: 70px 0 80px;
    
    @include media($md) {
        padding: 40px 0;
    }
    
    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;
        
        @include media($md) {
            grid-template-columns: repeat(2, minmax(100px, 300px));
            justify-content: center;
        }
        @include media($xs) {
            grid-template-columns: repeat(1, minmax(100px, 300px));
        }
    }
    
    &__img {
        width: 100%;
    }
}