.contact {
    padding: 15px 0;
    font-family: $font-pt;
    font-size: 12px;
    color: $contact-color;

    a {
        color: $contact-color;
    }

    &__content {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        @include media(390px) {
            justify-content: space-between;
        }
    }

    &__phone-text {
        @include media($sm) {
            display: none;
        }
    }

    .icon-phone {
        margin-right: 16px;

        @include media($sm) {
            margin: 0;
        }
    }

    .icon-enter {
        margin-right: 12px;

        @include media($sm) {
            margin: 0;
        }
    }

    &__group {
        display: flex;
        align-items: center;

        @include media(390px) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }

    &__item {
        margin-left: 13px;
        display: flex;
        align-items: center;

        &::before {
            content: '|';
            margin-right: 13px;

            @include media(390px) {
                content: '';
                display: none;
                margin: 0;
            }
        }

        @include media(390px) {
            margin: 0;


            &:first-of-type {
                position: relative;
                padding-bottom: 13px;
                margin-bottom: 13px;

                &::after {
                    content: '';
                    position: absolute;
                    height: 1px;
                    background: $contact-color;
                    bottom: 0;
                    width: 70%;
                    left: 0;
                    right: 0;
                    margin: 0 auto;
                }
            }


        }

    }

    &__enter {
        margin-left: auto;
        display: flex;
        align-items: center;

        @include media(390px) {
            margin-left: 0;
        }

        &-text {
            @include media($sm) {
                display: none;
            }
        }

    }
}