$fonts: ("fontawesome", "light", 300), 
        ("PlayfairDisplay", "Bold", 700),
        ("PlayfairDisplay", "Regular", 400),
        ("PostNoBillsJaffna", "Bold", 700),
        ("PTSans", "Bold", 700),
        ("PTSans", "Regular", 400);
        
@each $family, $name, $weight in $fonts {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}

$font-aw: "fontawesome";
$font-play: "PlayfairDisplay";
$font-post: "PostNoBillsJaffna";
$font-pt: "PTSans";
