.nav {
    background: $nav-bg;
    backdrop-filter: blur(15px);


    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;

        @include media(340px) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }
}

.menu {
    display: flex;
    margin-left: auto;


    &.active {
        // display: flex;
        transform: scaleY(1);
    }

    @include media($sm) {
        position: absolute;
        right: 0;
        top: 100%;
        flex-direction: column;
        background: $nav-bg;
        // display: none;
        transform: scaleY(0);
        transition: 0.5s;
        transform-origin: 0 0;
    }

    &__link {
        padding: 31px 20px;
        color: $menu-link;
        font-family: $font-pt;
        text-transform: uppercase;
        font-size: 14px;
        display: block;
        transition: 0.5s;

        @include media($sm) {
            padding: 15px 25px;
        }

        &:hover {
            color: $menu-link-hover;
            background: $menu-link-bg;
        }
    }

}

.service {
    display: flex;
    align-items: center;

    &__link {
        color: inherit;
        margin-left: 43px;

        @include media(340px) {
            margin: 10px 21px;
            display: block;
        }
    }

    li:last-of-type {
        display: none;

        @include media($sm) {
            display: block;
        }
    }
}

.header {

    &__content {
        background: url(../img/header-bg.jpg) center /cover;
        background-attachment: fixed;
        // min-height: 810px;
        text-align: center;
        font-family: $font-pt;
        padding: 15px 0;
    }

    &__info {
        @extend .flex;
        min-height: 80vh;
    }

    &__box {
        max-width: 320px;
        width: 100%;
        min-height: 320px;
        border: 4px solid $color;
        @extend .flex;

        @include media($xs) {
            max-width: 200px;
            min-height: 200px;
        }
    }

    &__logo {
        font-size: 64px;
        position: relative;

        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 2px;
            bottom: 14px;
            left: 0;
            position: absolute;
            background: $color;

            @include media($xs) {
                bottom: 7px;
            }
        }

        @include media($xs) {
            font-size: 45px;
        }
    }

    &__title {
        font-size: 22px;
        text-transform: uppercase;
        font-weight: 400;
        margin-top: -7px;

        @include media($xs) {
            font-size: 16px;
            margin-top: 0;
        }
    }

    &__desc {
        margin-top: 80px;
        font-size: 16px;
        line-height: 150%;
        text-transform: uppercase;
        max-width: 730px;
        width: 100%;

        @include media($xs) {
            margin-top: 40px;
        }
    }


}