.footer {
    background: #0F0F0F;

    &__content {
        padding: 10px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
        font-size: 20px;
    }

    &__desc {
        font-size: 12px;
        font-weight: 700;
        font-family: $font-pt;
        color: #D0D0D0;
    }
}